import { __assign } from "tslib";
import { get } from '@antv/util';
import { flow, renderStatistic } from '../../utils';
import { scale, animation, theme, annotation } from '../../adaptor/common';
import { geometry } from '../progress/adaptor';
import { PERCENT } from '../gauge/constants';
/**
 * coordinate 配置
 * @param params
 */

function coordinate(params) {
  var chart = params.chart,
      options = params.options;
  var innerRadius = options.innerRadius,
      radius = options.radius; // coordinate

  chart.coordinate('theta', {
    innerRadius: innerRadius,
    radius: radius
  });
  return params;
}
/**
 * statistic 配置
 * @param params
 */


export function statistic(params, updated) {
  var chart = params.chart,
      options = params.options;
  var innerRadius = options.innerRadius,
      statistic = options.statistic,
      percent = options.percent,
      meta = options.meta; // 先清空标注，再重新渲染

  chart.getController('annotation').clear(true);
  /** 中心文本 指标卡 */

  if (innerRadius && statistic) {
    var transformContent = statistic.content;

    if (transformContent && !transformContent.formatter) {
      // @ts-ignore
      transformContent.formatter = function (_a) {
        var percent = _a.percent;
        var metaFormatter = get(meta, [PERCENT, 'formatter']);

        if (metaFormatter) {
          return metaFormatter(percent);
        }

        return percent;
      };
    }

    renderStatistic(chart, {
      statistic: __assign(__assign({}, statistic), {
        content: transformContent
      }),
      plotType: 'ring-progress'
    }, {
      percent: percent
    });
  }

  if (updated) {
    chart.render(true);
  }

  return params;
}
/**
 * 环形进度图适配器
 * @param chart
 * @param options
 */

export function adaptor(params) {
  return flow(geometry, scale({}), coordinate, statistic, animation, theme, annotation())(params);
}