import { tooltip, interaction, animation, theme, scale, state, legend } from '../../adaptor/common';
import { flow, deepAssign } from '../../utils';
import { point } from '../../adaptor/geometries';
import { transform } from './utils';
/**
 * geometry 配置处理
 * @param params
 */

function geometry(params) {
  var chart = params.chart,
      options = params.options;
  var colorField = options.colorField,
      color = options.color;
  var data = transform(params);
  chart.data(data);
  var p = deepAssign({}, params, {
    options: {
      xField: 'x',
      yField: 'y',
      seriesField: colorField && 'color',
      point: {
        color: color,
        shape: 'word-cloud'
      }
    }
  });
  var ext = point(p).ext;
  ext.geometry.label(false);
  chart.coordinate().reflect('y');
  chart.axis(false);
  return params;
}
/**
 * meta 配置
 * @param params
 */


function meta(params) {
  return flow(scale({
    x: {
      nice: false
    },
    y: {
      nice: false
    }
  }))(params);
}
/**
 * 词云图适配器
 * @param chart
 * @param options
 */


export function adaptor(params) {
  // flow 的方式处理所有的配置到 G2 API
  flow(geometry, meta, tooltip, legend, interaction, animation, theme, state)(params);
}