import { theme, animation, annotation, tooltip } from '../../adaptor/common';
import { flow, deepAssign } from '../../utils';
import { interval } from '../../adaptor/geometries';
import { X_FIELD, Y_FIELD } from '../tiny-line/constants';
import { getTinyData } from '../tiny-line/utils';
import { meta } from '../tiny-area/adaptor';
export { meta };
/**
 * 字段
 * @param params
 */

function geometry(params) {
  var chart = params.chart,
      options = params.options;
  var data = options.data,
      color = options.color,
      columnStyle = options.columnStyle,
      columnWidthRatio = options.columnWidthRatio;
  var seriesData = getTinyData(data);
  chart.data(seriesData);
  var p = deepAssign({}, params, {
    options: {
      xField: X_FIELD,
      yField: Y_FIELD,
      widthRatio: columnWidthRatio,
      interval: {
        style: columnStyle,
        color: color
      }
    }
  });
  interval(p);
  chart.axis(false);
  chart.legend(false);
  chart.interaction('element-active');
  return params;
}
/**
 * 迷你柱形图适配器
 * @param chart
 * @param options
 */


export function adaptor(params) {
  return flow(geometry, meta, tooltip, theme, animation, annotation())(params);
}